.App {
  text-align: center;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background: linear-gradient(to bottom right, #6600cc 32%, #0066ff 100%);
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.header {
  text-align: center;
  color: #ffffff;
  display: flex;
  vertical-align: middle
}

.header p{
  margin: 0;
}

.header img{
  height: 65px;
  filter: invert(78%) sepia(59%) saturate(400%) hue-rotate(156deg) brightness(102%) contrast(103%);
  
}

.main {
  background-color: #000000;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  /* height: 500px; */
  border-radius: 20px;
  margin: 10px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.4);
  color: #ffffff;
}

.main img{
  /* margin: 10px; */
  border-radius: 10px;
}

.main input{
  /* margin: 10px; */
  background: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
  border:0;
  outline:0;
  color: #ffffff;
}
.main input:focus {
  outline:none!important;
}
.main button {
  border:0;
  outline:0;

}

.main button {
	box-shadow: 0px 10px 14px -7px #276873;
	background:linear-gradient(to bottom, #599bb3 5%, #408c99 100%);
	background-color:#599bb3;
	border-radius:8px;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:20px;
	font-weight:bold;
	padding:13px 32px;
	text-decoration:none;
	text-shadow:0px 1px 0px #3d768a;
}
.main button:hover {
	background:linear-gradient(to bottom, #408c99 5%, #599bb3 100%);
	background-color:#408c99;
}
.main button:active {
	position:relative;
	top:1px;
}

.footer {
  text-align: center;
  color: azure;
}

.footer p{
  margin: 0;
}

label {
  font-weight: 700;
}